body {
  background: #E8D7BB;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 20px;
  color: #101010;
}
.input-group {
  font-size: 20px;
  position: relative;
}
.input-group .password-eye {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
  cursor: pointer;
  z-index: 10;
  display: flex;
}
.input-log {
  all: unset;
  color: #3E1F0F;
  padding: 16px;
  border: 1px solid #3E1F0F;
  border-radius: 10px;
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
  box-sizing: inherit;
}
.label-log {
  position: absolute;
  top: 16px;
  left: 16px;
  color: #3E1F0F;
  pointer-events: none;
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
}
.input-log:focus {
  border: 1px solid #3E1F0F;
}
.input-log:is(:focus, :valid) ~ .label-log {
  transform: translateY(-120%) scale(0.7);
  background-color: #F8F7EE;
  padding-inline: 5px;
  color: #3E1F0F;
}
.auth-main {
  margin: auto;
  padding: 60px;
  width: fit-content;
  border-radius: 15px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  background-color: #F8F7EE;
}
.container {
  width: 100%;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.not-vertical-center {
  padding-top: 200px;
  align-items: start;
  display: block;
}
.title {
  text-align: center;
  font-size: 36px;
  font-family: 'Leckerli One', cursive;
  font-weight: 400;
  color: #3E1F0F;
}
.title-header {
  font-family: 'Leckerli One', cursive;
  font-size: 60px;
  text-align: center;
  font-weight: 400;
  color: #3E1F0F;
  margin: auto;
}
.header-position {
  position: absolute;
  right: 25px;
}
.button-style {
  background-color: #812F3A;
  border: none;
  color: #F8F7EE;
  padding: 15px 80px;
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  box-shadow: 0 10px 30px 0 rgba(62, 31, 15, 0.4);
  border-radius: 10px;
  margin: 15px 20px 0 20px;
  transition: all 0.3s ease-in-out;
}
.button-style:active {
  transform: scale(0.95);
}
.button-style:hover {
  background-color: #3E1F0F;
  cursor: pointer;
}
.link-log {
  text-align: center;
  margin: 10px auto 0;
  text-decoration: underline;
  cursor: pointer;
  width: fit-content;
}
.link-log:hover {
  color: #812F3A;
}
header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 120px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  background-color: #E8D7BB;
  margin: 10px 0;
  z-index: 999;
}
footer {
  width: 100%;
  height: 150px;
  background-color: #3E1F0F;
  color: #E8D7BB;
  display: flex;
  justify-content: space-between;
  padding: 40px;
  align-items: center;
}
.icon-footer {
  fill: #E8D7BB;
}
.icon-footer:hover {
  fill: #F8F7EE;
  cursor: pointer;
}
.icon-table {
  fill: #812F3A;
}
.icon-table:hover {
  fill: #3E1F0F;
  cursor: pointer;
}
/*modal popup*/
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.6s ease 0s;
  z-index: 9999;
}
.popup.open {
  opacity: 1;
  visibility: visible;
}
.popup.open .popup_content {
  transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
  opacity: 1;
}
.popup_body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
}
.popup_content {
  background-color: #F8F7EE;
  color: #3E1F0F;
  width: 800px;
  padding: 30px;
  position: relative;
  transition: all 0.6s ease 0s;
  opacity: 0;
  transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
}
.popup_close {
  position: absolute;
  right: 15px;
  top: 10px;
  font-size: 30px;
  color: #3E1F0F;
  text-decoration: none;
  font-weight: 600;
}
.popup_close:hover {
  color: #DBBF93;
  cursor: pointer;
}
.popup_title {
  font-size: 32px;
  margin: 0 0 1em 0;
  font-family: 'Leckerli One', cursive;
  font-weight: 400;
  text-align: center;
}
.popup_text {
  font-size: 24px;
  display: grid;
  width: fit-content;
  margin: auto;
}
.form-item {
  width: 600px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.form-item input {
  width: 280px;
  font-size: 20px;
  color: #3E1F0F;
  padding: 5px;
  border: 1px solid #3E1F0F;
  border-radius: 10px;
  text-align: center;
  margin-left: 10px;
}
.form-item .invalid {
  color: red;
  outline: none;
  border: 2px solid red;
}
.form-item .invalid:focus {
  border: 3px solid red;
}
.form-item div {
  display: grid;
  text-align: center;
}
.error-ad {
  color: red;
  font-size: 14px;
}
.input-search {
  color: #3E1F0F;
  background-color: #F8F7EE;
  padding: 10px;
  margin-left: 5px;
  font-size: 20px;
  border: 1px solid #3E1F0F;
  border-radius: 10px;
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
}
