.table-div {
  max-width: 1400px;
  margin: auto;
  max-height: 700px;
}
.table-box {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  align-items: center;
}
.button-table {
  font-size: 40px;
  padding: 0 12px;
  margin: 0;
}
.tooltipWrapper {
  position: relative;
  display: inline-block;
}
.targetElement {
  cursor: pointer;
}
.tooltip {
  position: absolute;
  padding: 10px;
  border-radius: 3px;
  overflow: hidden;
  background-color: #F8F7EE;
  width: 300px;
  font-size: 12px;
  box-sizing: border-box;
  box-shadow: 1px 1px 10px #DBBF93;
  z-index: 9999;
}
.tooltip.top {
  bottom: 130%;
  transform: translateX(-50%);
  left: 50%;
}
.tooltip.bottom {
  top: 130%;
  transform: translateX(-50%);
  left: 50%;
}
.tooltip.right {
  left: 130%;
  transform: translateY(-50%);
  top: 50%;
}
.tooltip.left {
  right: 130%;
  transform: translateY(-50%);
  top: 50%;
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-toggle {
  cursor: pointer;
}
.dropdown-menu {
  display: block;
  position: absolute;
  background-color: #f0e4d1;
  min-width: 160px;
  box-shadow: 1px 1px 10px #DBBF93;
  z-index: 1;
  right: 0;
  /* Выровнять по правой стороне */
  top: 130%;
  border-radius: 10px;
}
.dropdown-item {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  border-radius: 10px;
}
.dropdown-item:hover {
  background-color: #F8F7EE;
  cursor: pointer;
}
.pedro {
  width: 400px;
}
.pedro img {
  width: 400px;
}
.trash {
  fill: #F8F7EE;
  width: 25px;
  height: 25px;
}
